import React from "react";

const CardModal = ({ isOpen, onClose, cardInfo}) => {
  if (!isOpen || !cardInfo) {
    return null;
  }

  const {
    name,
    upright,
    uprightsalud,
    uprightdinero,
    uprightamor,
    reversed,
    reversedsalud,
    reverseddinero,
    reversedamor,
    isReversed,
  } = cardInfo;
  
  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content card-modal" onClick={(e) => e.stopPropagation()}>
        <button className="modal-close" onClick={onClose}>
          &times;
        </button>
        <h3>{name}</h3>
        <hr></hr>
        <p className="significado keywords">
          {isReversed ? reversed : upright}
        </p>
        <hr></hr>
        <p className="significado salud">
          {isReversed ? reversedsalud : uprightsalud}
        </p>
        <p className="significado dinero">
          {isReversed ? reverseddinero : uprightdinero}
        </p>
        <p className="significado amor">
          {isReversed ? reversedamor : uprightamor}
        </p>
        <hr></hr>
        <a href="https://w.app/quintaesencia" target="_blank" rel="noopener noreferrer" className="contact-link">
          <img className="logo" src={process.env.PUBLIC_URL + "/logo192.png"} alt="Logo" />
          <span className="logo-texto">¿Necesitas una lectura personalizada?</span>
          <img className="logo" src={process.env.PUBLIC_URL + "/logo192.png"} alt="Logo" />
        </a>
      </div>
    </div>
  );
  
};

export default CardModal;
