import React, { useState,useEffect } from "react";
import "./css/App.css";
import "./css/normalizer.css";
import cardImages from "./blocks/cardImages";
import CardModal from "./blocks/CardModal";


const App = () => {
  const [selectedCardCount, setSelectedCardCount] = useState(1);
  const [randomImages, setRandomImages] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedCardInfo, setSelectedCardInfo] = useState(null);
  const [isFirstDraw, setIsFirstDraw] = useState(true); 
  const [cardClasses, setCardClasses] = useState([]);
  const [isButtonClicked, setIsButtonClicked] = useState(false); 
  const [showPreload, setShowPreload] = useState(true);
  const [selectedDeck, setSelectedDeck] = useState("full");

  const handleDeckSelect = (event) => {
    setSelectedDeck(event.target.value);
  };
  


  useEffect(() => {
    const imagesToPreload = cardImages.map(card => card.image);
    const preloadImages = [];
    
    imagesToPreload.forEach(src => {
      const img = new Image();
      img.src = src;
      preloadImages.push(img);
      const preloadTimeout = setTimeout(() => {
        setShowPreload(true);
      }, 1000);
      return () => {
        clearTimeout(preloadTimeout); // Limpia el timeout si el componente se desmonta antes del retraso
      };
    });
  
    Promise.all(preloadImages.map(img => img.decode()))
      .then(() => {
        setShowPreload(false); // Oculta la animación de preload una vez que las imágenes estén cargadas
      })
      .catch(error => {
        console.error("Error preloading images:", error);
      });
  }, []); // Se ejecuta solo una vez al montar el componente

  const handleDrawCard = () => {
    const now = new Date();
    let timestamp = now.getTime();
    const drawnImages = [];
    const excludedIndexes = [0];
    let cardSubset = [];

    switch (selectedDeck) {
      case "major":
        cardSubset = cardImages.slice(0, 22); // Arcanos Mayores (1-22)
        break;
      case "minor":
      cardSubset = cardImages.slice(22); // Arcanos Menores (23-78)
      break;
      case "bastos":
        cardSubset = cardImages.slice(23,36); // bastos
        break;
      case "copas":
        cardSubset = cardImages.slice(37,50); // copas
        break;
        case "oros":
      cardSubset = cardImages.slice(51,64); // oros
      break;
      case "espadas":
      cardSubset = cardImages.slice(65,78); // Espadas
      break;
      case "corte":
      const specificCardIndexes = [33, 34, 35, 36, 47, 48, 49, 50, 61, 62, 63, 64, 75, 76, 77, 78];
      specificCardIndexes.forEach((index) => {
        if (index >= 0 && index < cardImages.length) {
          cardSubset.push(cardImages[index]);
        }
      });
      break;
      default:
        cardSubset = cardImages; // Baraja Completa (1-78)
        break;
    }

    while (drawnImages.length < selectedCardCount) {
      const randomIndex = Math.floor(Math.random() * cardSubset.length);
      const uniqueIndex = (randomIndex + timestamp) % cardSubset.length;
  
      if (!drawnImages.includes(uniqueIndex) && !excludedIndexes.includes(uniqueIndex)) {
        drawnImages.push(uniqueIndex);
        timestamp++;
      }
    }

    const imagesToDisplay = drawnImages.map((index, cardIndex) => {
      const realIndex = index % cardSubset.length;
      const card = cardSubset[realIndex];
      let className = "";
      const isReversed = Math.random() < 0.22;

      return {
        image: card.image,
        name: card.name,
        upright: card.upright,
        reversed: card.reversed,
        uprightsalud: card.uprightsalud,
        uprightdinero: card.uprightdinero,
        uprightamor: card.uprightamor,
        reversedsalud: card.reversedsalud,
        reverseddinero: card.reverseddinero,
        reversedamor: card.reversedamor,
        className,
        isReversed,
      };
    });
    
    // Set fade-out CSS class to all cards
    const randomCardsDiv = document.querySelector(".random-cards");
    randomCardsDiv.classList.add("fade-out");
    setTimeout(() => {
      // Set fade-in CSS
      randomCardsDiv.classList.remove("fade-out");
      randomCardsDiv.classList.add("fade-in");
      setRandomImages(imagesToDisplay);
    },0)
    setIsFirstDraw(true);
  };

  const handleFormSubmit = (event) => {
    event.preventDefault();
    handleDrawCard();
    setIsFirstDraw(false);
  };

  const handleCardClick = (card) => {
    setSelectedCardInfo(card);
    setIsModalOpen(true);
  };

  
  const generateCardClasses = () => {
    if (isButtonClicked) {
      switch (selectedCardCount) {
        case 1:
          return "una-carta";
        case 2:
          return "dos-cartas";
        case 3:
          return "tres-cartas";
        default:
          return "";
      }
    }
    return "";
  };
  

  const getSelectedCardText = () => {
    switch (selectedCardCount) {
      case 1:
        return "Utiliza esta carta como consejo o respuesta.";
      case 2:
        return "Utiliza las cartas como dos personajes o en resonancia.";
      case 3:
        return "Utiliza esta tirada como pasado, presente y futuro.";
        default:
        return "";
    }
  };
  
  const handleEcharCartasClick = () => {
    setIsFirstDraw(false);
    setIsButtonClicked(true);
  
    // Generar las clases antes de dibujar las cartas
    const newCardClasses = randomImages.map((_, index) =>
      generateCardClasses(index, selectedCardCount)
    );
    setCardClasses(newCardClasses);
  
    handleDrawCard(); // Dibujar las cartas después de generar las clases
  };
  
  
  

  return (
    <div className="app">
      {showPreload && <div className="preload-animation"></div>}
      <div id="title">
        <h2>Enfócate</h2>
        <h3>en tu pregunta</h3>
      </div>
      <form onSubmit={handleFormSubmit}>

    <div className="select-container">   
      <div className="select">
          <select
            value={selectedCardCount}
            onChange={(event) => setSelectedCardCount(Number(event.target.value))}
          >
            <option value={1}>Tirada De Una Carta</option>
            <option value={2}>Tirada De Dos Cartas</option>
            <option value={3}>Tirada De Tres Cartas</option>
          </select>
      </div>
      
      <div className="select">
        <select
          value={selectedDeck}
          onChange={handleDeckSelect}
        >
          <option value="full">Baraja Completa</option>
          <option value="major">Arcanos Mayores</option>
          <option value="minor">Arcanos Menores</option>
          <option value="corte">Figuras De La Corte</option>
          <option value="bastos">Bastos</option>
          <option value="copas">Copas</option>
          <option value="oros">Oros</option>
          <option value="espadas">Espadas</option>
        </select>
      </div>
    </div>

      <p className="legend tirada">{getSelectedCardText()}</p>
      <div className="random-cards">
        {isFirstDraw ? ( // Mostrar la imagen de reverso en el primer dibujo
          <div className="card back">
            <img src={cardImages[0].image} alt="Reverso de la carta" />
          </div>
        ) : (
          randomImages.map((card, index) => (
            <div
              key={index}
              className={`card ${generateCardClasses()}`} 
              onClick={() => handleCardClick(card)}
            >
              <img
                src={card.image}
                alt={card.name}
                className={card.isReversed ? "reversed" : ""}
              />
            </div>
          ))
          
        )}
      </div>
      <p className="legend">Haz click o tap sobre la carta para conocer su significado.</p>
      
      {isModalOpen && (
        <CardModal
          isOpen={isModalOpen}
          onClose={() => setIsModalOpen(false)}
          cardInfo={selectedCardInfo}
        />
      )}
      <button className="echar-las-cartas"
        onClick={handleEcharCartasClick}>Echar las cartas</button>
      </form>
      <footer>
        <p className="version">Tu Quitaesencia - Tarot Online v3.5</p>
      </footer>
    </div>
    
  );  
};

export default App;